import React from 'react';
import {Navbar,Nav,Carousel} from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import car1 from '../images/soe.jpg';
import car2 from '../images/soe_cm.jpg';
import '../css/header.css'
import Footer from './footer';
class home extends React.Component {
    home = () => {
        this.props.history.push("/sih/");
    }
    login = () => {
        this.props.history.push("/sih/login/");
    }
    sc = () => {
        this.props.history.push("/sih/instutions/");
    }
    render()
    {
        
    return (
    <div>
        <Navbar collapseOnSelect expand="lg" bg="primary" variant="dark" sticky="top">
            <Navbar.Brand href="#home">Govt of Andhra Pradesh</Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="mr-auto">
                <Nav.Link href="#" className="text-white" onClick={this.home}>Home</Nav.Link>
                <Nav.Link href="#" className="text-white" onClick={this.login}>Login</Nav.Link>
                <Nav.Link href="#" className="text-white" onClick={this.sc}>Institutions</Nav.Link>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
        <br/>
        <div className="container">
        <center>
        <Carousel className="car">
            <Carousel.Item>
                <img className="d-block w-100" src={car1}  height="200"/>
            </Carousel.Item>
            <Carousel.Item>
                <img className="d-block w-100" src={car2} height="200"/>
            </Carousel.Item>
        </Carousel>
        </center>
        </div>
        <br/>
        <div className="container-fluid">
            
        <div className="card alert-info"> 
        <h3 className="card-header text-center text-dark"><i>Home</i></h3>
        <p className="alert text-justify"><i> The Department of Higher Education deals with matters relating to education at various levels in the State. The Higher Education Department in the State deals with higher education i.e., college education, technical education and universities. Objectives of the department are development of Undergraduate and Post graduate education, Increasing Access to Higher Education, encouraging private participation in the expansion of Collegiate Education, development of infrastructure in Government Colleges, ensuring maintenance of high standards of education in colleges.</i></p>
        </div>
        <br/>
        <div className="card " height="50px"> 
        <h4 className="card-header alert-danger text-center text-dark"><i>Updates</i></h4>
        <marquee height="100px" scrollamount="3" direction="up" >
            <div className="alert alert-info"><i>All Educational Institutions are closed till May 3,2020.</i></div>
            <div className="alert alert-info"><i>Stay Home,Stay Safe</i></div>
        </marquee>
        </div>
        </div>
        <Footer/>
    </div>
  );
    }
}

export default home;
