import React from 'react';
import Home from './pages/home';
import Login from './pages/login';
import Instution from './pages/instution';
import Hoe from './pages/hoe/home';
import Assign_task from './pages/hoe/assign_task';
import Assigned_task from './pages/hoe/view_assigned_task';
import Admin_view_feedback from './pages/hoe/admin_view_feedback';
import New_User from './pages/hoe/create_new_user';
import Vo from './pages/vo/home';
import Assigned_task_vo from './pages/vo/view_assigned_task';
import Give_feedback from './pages/vo/give_feedback';
import Ins from './pages/ins/home';
import View_visiter_details from './pages/ins/view_visiter_details';
import View_feedback from './pages/ins/view_feedback';
import 'bootstrap/dist/css/bootstrap.min.css';
import {BrowserRouter,Route} from 'react-router-dom';
function App() {
  return (
    <div>
      <BrowserRouter>
      <Route exact path={"/sih/"} component={Home} />
      <Route exact path={"/sih/login/"} component={Login} />
      <Route exact path={"/sih/instutions/"} component={Instution} />
      <Route exact path={"/sih/hoe/"} component={Hoe} />
      <Route exact path={"/sih/assign_task/"} component={Assign_task} />
      <Route exact path={"/sih/view_assigned_task/"} component={Assigned_task} />
      <Route exact path={"/sih/admin_view_feedback/"} component={Admin_view_feedback} />
      <Route exact path={"/sih/create_new_user/"} component={New_User} />
      <Route exact path={"/sih/vo/"} component={Vo} />
      <Route exact path={"/sih/vo/view_assigned_task/"} component={Assigned_task_vo} />
      <Route exact path={"/sih/give_feedback/"} component={Give_feedback} />
      <Route exact path={"/sih/ins/"} component={Ins} />
      <Route exact path={"/sih/ins/view_visiter_details/"} component={View_visiter_details} />
      <Route exact path={"/sih/ins/view_feedback/"} component={View_feedback} />
      
      </BrowserRouter>
    </div>
  );
}

export default App;
