import React from 'react';
import $ from 'jquery';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../css/header.css'
class header extends React.Component {
    footshow = () =>
    {
        $('#foot').show();
    }
    foothide = () =>
    {
        $('#foot').hide();
    }
    render()
    {
        
  return (
    <div>
        <br/>
        <div className="bg-primary text-center text-white"><i>Designed by <a id="footer" onClick={this.footshow} href="#" className="text-warning"><b>Hypertext Techies</b></a></i></div>
        <div id="foot" class="modal">
           <div class="modal-content">
           <span className="text-right"><p id="close" class="close " onClick={this.foothide}>&times;</p></span>
                <div className="card">
                
                    <div className="card-header">
                        <span className="text-center text-dark"><h3><i>Hypertext Techies</i></h3></span>
                        
                    </div>
                    <div className="card-body">
                        <p className="text-primary"><h4>Mentor</h4></p>
                        <h6><li className="text-dark">Mr.K.R.Prasanna Kumar,AP/IT,KEC</li> </h6>
                        <p className="text-primary"><h4>Team Members</h4></p>
                        <h6> <li className="text-dark">Mr.B.Sri Hari Harran,KEC</li></h6>
                        <h6> <li className="text-dark">Mr.N.Rathu,KEC </li></h6>
                        <h6><li className="text-dark">Mr.M.K.Stalin,KEC</li></h6>
                        <h6><li className="text-dark">Miss.S.Nivashini,KEC</li></h6>
                        <h6><li className="text-dark">Miss.S.K.Showentharya,KEC </li></h6>
                        <h6><li className="text-dark">Miss.S.Swetha Jayashree,KEC </li></h6>
                    </div>
                </div>
            </div>
        </div>
    </div>
  );
    }
}

export default header;
