import React from 'react';
import {Navbar,Nav} from 'react-bootstrap'
import $ from 'jquery';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../css/header.css'
import Footer from './footer';
import User from './User';
import { browserHistory } from 'react-router';
class login extends React.Component {
    constructor()
    {
        super();
       
    }
    home = () => {
        this.props.history.push("/sih/");
    }
    login = () => {
        this.props.history.push("/sih/login/");
    }
    sc = () => {
        this.props.history.push("/sih/instutions/");
    }
    hoe = () => {
        this.props.history.push("/sih/hoe/");
    }
    vo = () => {
        this.props.history.push("/sih/vo/");
    }
    ins = () => {
        this.props.history.push("/sih/ins/");
    }
    submit = (e) => {
        e.preventDefault();
        $.ajax({
            url: 'https://hypertexttechies2020.000webhostapp.com/SIH/feedback/auth.php',
            type: "POST",
			data: $('#login').serialize(),
			
            success: function(data) {
                
                var res=data.split(",");
                alert(res[0]);
                localStorage.setItem('name', res[1]);
                if(res[0]=="Head of Education")
                {
                    localStorage.setItem('key', 'hoe');
                    $('#hoe').click();
                }
                if(res[0]=="Visiting Officer")
                {
                    localStorage.setItem('key', 'vo');
                    $('#vo').click();
                }
                if(res[0]=="Institution")
                {
                    localStorage.setItem('key', 'vo');
                    $('#ins').click();
                }
            },
            error: function(xhr, status, err) {
                alert("Error");
            }
        });
        
    }
    render()
    {
        
    return (
    <div>
        <Navbar collapseOnSelect expand="lg" bg="primary" variant="dark" sticky="top">
            <Navbar.Brand href="#home">Govt of Andhra Pradesh</Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="mr-auto">
                <Nav.Link href="#" className="text-white" onClick={this.home}>Home</Nav.Link>
                <Nav.Link href="#" className="text-white" onClick={this.login}>Login</Nav.Link>
                <Nav.Link href="#" className="text-white" onClick={this.sc}>Institutions</Nav.Link>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
        <br/>
        <div className="container">
            
            <div className="card alert-primary">
                <div className="card-header text-center"><h3><i>Login</i></h3></div>
                <div className="card-body">
                    <form id="login" >
                        <div className="form-group">
                            <label>Type of User:</label>
                            <select className="form-control" name="user_type" id="user_type">
                                <option value="Head of Education">Head of Education</option>
                                <option value="Visiting Officer">Visiting Officer</option>
                                <option value="Institution">Institution</option>
                                
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Username:</label>
                            <input type="text" className="form-control" name="username" id="username" />
                        </div>
                        <div className="form-group">
                            <label>Password:</label>
                            <input type="password" className="form-control" name="password" id="password" />
                        </div>
                        <div className="form-group text-center">
                            <input type="submit" className="btn btn-primary" name="submit" id="submit" value="Login" onClick={this.submit} />
                        </div>
                        <div className="form-group text-center text-danger">
                            Forgot Password?
                        </div>
                        <button onClick={this.hoe}  id="hoe">hoe</button>
                        <button onClick={this.vo} id="vo">vo</button>
                        <button onClick={this.ins} id="ins">ins</button>
                    </form>
                </div>
            </div>
        </div>
        <Footer/>
    </div>
  );
    }
}

export default login;
